

import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import getDataService from '../../../service/getData.js'
import upDataSevice from '../../../service/upData.js'

  import msgTitle from '../../../components/title.vue'
  import userName from '../../../components/userName.vue'
  // import specialDye2 from '../specialDye/specialDye2.vue'
  import Toast from 'vux/src/components/toast/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

  import serviceGetData from '../../../service/getData.js'

  export default {
    components:{
      msgTitle, userName, Checker, CheckerItem,Toast,XTextarea,XHeader,XInput,PopupPicker,
      Group,
      Checklist,
      Cell,
      Divider,
      XButton,
    },
    created(){

      getDataService.dyeType().then(res=>{
        var arr=[];
        for(var i in res){
          arr.push({value:''+res[i].code , name:res[i].name});
        }
        this.dyeTypeData = arr

      },res=>console.log(res))

      try{
        // console.log(this.$store.state.applicant.default,1111)
        if(this.$store.state.applicant.default){ // 
          this.cur_name = this.$store.state.applicant.default.firstName
          return
        }else{

          // 
          serviceGetData.applicant_default_name({isSelf:true}).then(res=>{
            console.log(res.data[0])
            this.$store.state.applicant.default=res.data[0];
            this.cur_name=res.data[0].firstName
          },res=>console.log(res))

        }
      }catch(e){
        console.log('',e)
      }

    },
    mounted(){

      console.log('deviceid: ', this.data=this.$route.query.routeParams);

      var vm=this;
      var start = async function () {
          // var createTimeEnd=(+new Date());
          // var createTimeStart=(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[vm.select_date-1]; // 
          var data=await serviceGetData.blocks({
            createTimeEnd:(+new Date()),
            createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[vm.select_date], // 
            length:vm.select_length,
            page:vm.select_page
          });
          vm.checkbox_val=[] // [data.data[0].blockId] // 
          vm.blocks=data.data;
      };

      start();
    },
    methods: {
      select_date_fn(date){
        this.select_date=date
        this.blocks=[];

        var vm=this;
        var start = async function () {
            // var createTimeEnd=(+new Date());
            // var createTimeStart=(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[vm.select_date-1]; // 
            var data=await serviceGetData.blocks({
              createTimeEnd:(+new Date()),
              createTimeStart:(+new Date())-{1:2592000000, 2:7776000000, 3:31536000000}[vm.select_date], // 
              length:vm.select_length,
              page:vm.select_page
            });
            vm.checkbox_val=[] // [data.data[0].blockId] // 
            vm.blocks=data.data;
        };

        start();

      },
      change (val) {
        console.log('change', val)
      },
      next(){
        var checkbox_val = this.checkbox_val;
        if( checkbox_val.length == 0 ){
          this.show_Toast=true
          this.Toast_txt=''
        }else{

          // 
            var blocks = this.blocks;
            console.log(blocks,222)

            if( blocks.length > 0 ){

              var arr=[]

              for(var i=0 ; i <= checkbox_val.length ; i++){
                for(var j=0; j <= blocks.length-1; j++){
                  if( checkbox_val[i] == blocks[j].blockId){
                    arr.push(blocks[j])
                  }
                }
              }

            }

            this.$store.commit('specialDye',[arr])


          // 
            // this.$router.push({
            //       name: 'specialDye2',
            //       query: {
            //         // routeParams: this.checkbox_val
            //       }
            //   })
        }

      },

      onNoError () {
        this.error = null
      },
      onError (errors) {
        console.log(errors)
        this.error = errors
      },

      onHide(){
        // this.$router.push({name:"addApplicant"});
      },
      save(){
        this.next()

        console.log(this.user_yeType)

        if( this.user_yeType.ihcMarker == ''){

          this.toastText='';
          this.toastShow=true;
        }else{

          var applicantId = this.$store.state.applicant.default.id
          var specialDye_arr = this.$store.state.specialDye[0]

          // 
            var ihcMarker_arr=[];
            var str = this.user_yeType.ihcMarker; // 
            str = str.replace(/\s+/g, ' ') // 
            .replace(/[;]+/g, ';')// ;;
            .replace(/; /g, ';') // 
            .replace(/ ;/g, ';') // 
            .replace(/;+/g, ';') // 
            var ihcMarker_arr = str.split(';')


          // 
            var ihcBlocks_arr = [];
            for(var i=0; i<= specialDye_arr.length-1; i++){
              var data = specialDye_arr[i];
              ihcBlocks_arr.push({
                serialNumber : (data.pathologySerialNumber), // #
                subId: (data.subId)+'', // #
                specialDye: (this.user_yeType.specialDye)[0] ? parseInt((this.user_yeType.specialDye)[0]) : '' , // # ,  NAN
                ihcMarker: ihcMarker_arr, // #
                note: (this.user_yeType.note) // #

              })
            }

         // 
            var toService={
              "applicantId": applicantId,
              "ihcBlocks":ihcBlocks_arr
            }

            console.log(toService)

         // 
          upDataSevice.up_specialDye(toService).then(res=>{
            this.toastText='';
            this.toastShow=true;
            location.reload()
            // history.go(-1)
          },res=>{

            this.toastText='';
            this.toastShow=true;
            console.log(res)
          })


        }
      }

    },
    data () {
      return {
        select_date:1, //  1 
        select_page:1, // 
        select_length:10, // 

        cur_name:'',
        error: '',
        blocks: [], // 
        checkbox_val: [], // 
        show_Toast: false,
        Toast_txt:'',


        data:null,
        dyeTypeData:[], // 
        user_yeType:{
            // "applicantId":1, // ID
            "specialDye":[], // 
            "ihcMarker":'', // 
            "note":"", // 
          },
        toastText:'',
        toastShow:false,


      }
    },
    computed:{
      checkbox_val_more(){},
      ihcMarker_arr(){ // 
      }
    }
  }




